import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import { getDormitorys, deleteDormitory } from 'services/dormitory';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';

class DormitoryBuilding extends Component {
  state = {
    reload: 0,
  };
  handleDelete = async id => {
    await deleteDormitory(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      {
        title: 'Mã tòa nhà',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên tòa nhà',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Số tầng',
        dataIndex: 'noOfFloor',
        key: 'noOfFloor',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.DANH_MUC__KTX__TOA_NHA__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Tòa nhà KTX">
        <CartHeadAction>
          <Link to={routeConstant.DANH_MUC__KTX__TOA_NHA__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getDormitorys}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <DormitoryBuilding />
  </Page>
);



